import React, { useState } from 'react';
import Button from '@mui/material/Button';

interface CountProps {
  text: string;
  countState: number;
}

interface FieldProps {
  isUseMemo: boolean;
}

// FieldcountState
const MemoCount: React.FC<CountProps> = React.memo(({ text, countState }) => {
  console.log('Count child component:', text);
  return (
    <p className="py-1 text-xl">
      {text}: {countState}
    </p>
  );
});

const Count: React.FC<CountProps> = ({ text, countState }) => {
  console.log('Count child component:', text);
  return (
    <p className="py-1 text-xl">
      {text}: {countState}
    </p>
  );
};

const Field: React.FC<FieldProps> = (props) => {
  const { isUseMemo } = props;
  const [countA, setCountA] = useState<number>(0);
  const [countB, setCountB] = useState<number>(0);

  return (
    <>
      {isUseMemo ? (
        <>
          <MemoCount text="A " countState={countA} />
          <p className="attention-red">{countA}</p>
          <MemoCount text="B " countState={countB} />
          <p className="attention-red">{countB}</p>
        </>
      ) : (
        <>
          <Count text="A " countState={countA} />
          <p className="attention-red">{countA + countB}</p>
          <Count text="B " countState={countB} />
          <p className="attention-red">{countA + countB}</p>
        </>
      )}
      <div className="p-4">
        <Button variant="outlined" onClick={() => setCountA(countA + 1)}>
          A 
        </Button>
        <Button variant="contained" onClick={() => setCountB(countB + 1)} sx={{ marginLeft: '8px' }}>
          B 
        </Button>
      </div>
    </>
  );
};

export const MemoComponent: React.FC = () => {
  return (
    <>
      <div className="description">
        <div className="description-left">
          <p className="description-left-p">React.memo</p>
          <p className="description-left-p">
            <span className="hidden md:inline"></span>
            <span className="inline md:hidden"></span>React.memo
          </p>
          <p className="description-left-p">
            React.memoAB
          </p>
          <h3 className="description-left-h3"></h3>
          <ul className="description-left-ul">
            <li></li>
            <li></li>
          </ul>
        </div>
        <div className="description-right">
          <div className="description-right-container">
            <h3 className="description-right-container-h3">React.memo</h3>
            <Field isUseMemo={true} />
          </div>
          <div className="description-right-container">
            <h3 className="description-right-container-h3">React.memo</h3>
            <Field isUseMemo={false} />
          </div>
        </div>
      </div>
    </>
  );
};
